    import {mapState, mapMutations} from 'vuex';
	import {findByID, getAllSocialInfo, upPicture} from '../../api/api.js';

    export default {
		data() {
			return {
				userInfo: '',
				socialInfo: '',
				fans: '18296',
				focus: '13565',
				photo: '345',
				commune: '3'
			}
		},
        computed: {
            ...mapState(['hasLogin', 'forcedLogin'])
        },
        methods: {
            ...mapMutations(['logout']),
            bindLogout() {
                this.logout();
				uni.reLaunch({
					url: '../login/login'
				})
				uni.clearStorage('USERS_KEY');
            },
			getUserInfo() {
				findByID().then(data => {
					this.userInfo = data;
				});
			},
			getSocialInfo() {
				getAllSocialInfo().then(data => {
					this.socialInfo = data;
				});
			},
			uploadAvatar() {
				upPicture(uni.getStorageSync('USERS_KEY').id).then(data => {
					if(data == 'success') {
						findByID().then(data => {
							this.getUserInfo();
						});
					}
				});

			}
        },
		onShow() {
			this.getUserInfo();
			this.getSocialInfo();
		}
    }
